@use "global" as *;

.blog-page-detail {
  position: relative;
}

.blog-page-detail__inner {
  position: relative;
}

.blog-page-detail__hero {
  position: relative;
}


.blog-page-detail__title {
  margin-top: rem(8);
}

.blog-page-detail__contents {
  margin-top: rem(52);

  @include mq(md) {
    margin-top: rem(150);
  }
}

.blog-page-detail__content {
  display: flex;
  flex-direction: column;

  @include mq(md) {
    display: flex;
    flex-direction: column;
  }
}

.blog-page-detail__img {
  margin-top: rem(24);

  @include mq(md) {
    margin-top: rem(40);
  }
}

.blog-page-detail__img img {
  object-fit: cover;
}

.blog-page-detail__text {
  margin-top: rem(40);
  font-size: rem(16);
}

.blog-page-detail__list {
  margin-top: rem(40);
}

.blog-page-detail__list li{
  position: relative;
  margin-left: rem(27);
}

.blog-page-detail__list li::before {
  content: "";
  display: block;
  position: absolute;
  background-image: url("../images/common/hitode.svg");
  width: rem(16);
  height: rem(16);
  background-repeat: no-repeat;
  left: rem(-23);
  top: 50%;
  transform: translate(0 , -50%);
}

.blog-page-detail__button {
  margin-top: rem(40);
  @include mq(md) {
  margin-top: rem(80);
  }
} 